import React from 'react';
import { StyleSheet, View } from "react-native";
import { List } from "react-native-paper"

export default function Teams({ match }) {

  return (
    <View>
      <List.Section title="Equipo 1">
        <List.Subheader title="Equipo 1">
          {match.team1.map((player, index) => {
            return (
              <List.Item key={index} title={player.apodo} />
            );
          })}
        </List.Subheader>
      </List.Section>
      <List.Section title="Equipo 2">
        <List.Subheader title="Equipo 2">
          {match.team2.map((player, index) => {
            return (
              <List.Item key={index} title={player.apodo} />
            );
          })}
        </List.Subheader>
      </List.Section>
    </View>

  );
}

const styles = StyleSheet.create({
  container: {
    padding: 10,
    height: '100%',
    backgroundColor: '#fff',
    right: 80
  }
});